@import "../../library/colors";
@import "../../library/mixins";

.about-page-container {
  height: 100vh;
  @include flex-div-centered;
}

.about-div {
  width: 72%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  bottom: 100px;
  position: relative;
}

.about-text-container {
  margin-top: -2%;
  margin-left: 5%;
  padding: 5px 30px;
  background-color: #a40e88;
  z-index: 5;
}

.about-heading {
  font-size: 40px;
  color: white;
  margin: 0;
}

.about-description {
  font-family: 'Circular', sans-serif;
  font-size: 24px;
  color: #1e3264;
  margin-top: 52px;
  padding: 0 35px;
  text-align: justify;
}

.about-background {
  background-color: #a40e88;
  opacity: 0.08;
  width: 100%;
  height: 475px;
  position: absolute;
}
